import { useContext, useEffect, useRef } from "react"
import Context from "../../../Context/Context"
import React from "react"
import getPosts from "../../../hooks/getPosts"
import move from "../../../hooks/move"
import { Link } from "react-router-dom"
import {useDispatch} from 'react-redux';
import './login.css'

function MobileLogin(prop){
    const dispatch = useDispatch()
    let email = useRef()
    let pass = useRef()
    let closer = useRef()
    const hideBlock = useRef()
    let iteme = useRef()
    
    const {usersDate, register} = useContext(Context)
    useEffect(()=>{
        move(register, iteme, hideBlock)
        dispatch({type: 'SET_CI', log: hideBlock, hide: iteme}) 
    }, [iteme])



    async function Reg(){
        usersDate.forEach(item=>{
            if(email.current.value == item.email && pass.current.value == item.password){
                    dispatch({type: 'SET_C_U', item: item})
                    move(true, iteme, hideBlock)
                    dispatch({type:'SET_IR'})
                    getPosts(posts)
                    

            }
        })
    }

    function posts(arg){
        dispatch({type: 'SET_P', item: arg})
        dispatch({type: 'SET_I_P'})
    }
    return(
<>
            <div ref={hideBlock} className="hide_window" style={{display: 'flex'}}></div>
        <div ref={iteme} className="login_window">
        <div className="container">
                          
            <h2 className="login_lable">Log in</h2>
            <input ref={email} type="text" className="login_in bio_inputs" placeholder="Login"/>
            <input ref={pass} type="text" className="password_in" placeholder="Password"/>
            <div className="reg_active" onClick={()=> prop.setRegister(true)}>Create account</div>
            <Link to='/bio' className="log_but" onClick={()=> Reg()}>Enter</Link>
        </div>
        </div>
        </>
    )
}
export default MobileLogin