
import { useContext} from 'react';
import '../App.css';
import Bio from '../Bio/Bio';
import Contacts from '../Contacts/Contacts';
import Main from '../Main/Main';
import React from 'react';
import Context from '../Context/Context';
import { useNavigate } from 'react-router';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import getPosts from '../hooks/getPosts';
import { CheckPage } from '../hooks/checkPage';

function General() {
  const {currentUser} = useContext(Context)
let dispatch = useDispatch()
let navigate = useNavigate()


useEffect(()=>{
  dispatch({type: 'REMOVE_TR'})
  // CheckPage('general', register, isTrade, currentUser)
  getPosts(commit, JSON.parse(localStorage.getItem('postsDate')))
  console.log(currentUser);


}, [])

function commit(arg, connection = true){
  if(arg){
    dispatch({type: 'SET_P', item: arg})
    dispatch({type: 'SET_I_P', item: true})
  }
  if(!connection){
    dispatch({type:'SET_CN', item: false})
  } 
  
}





  return (
    <>
        <Contacts/>
        <Main/>
        {window.screen.width > 500? <Bio />: ''}
    </>
  );
}

export default General;
