import { useEffect} from 'react';
import './App.css';
import Treads from './pages/Treads';
import General from './pages/General';
import React from 'react';
import Context from './Context/Context';
import getUsers from './hooks/getUsers';
import Registrate from './pages/Registrate';
import {Routes, Route} from 'react-router-dom'
import TreadsBl from './pages/TreadsBl';
import {useSelector} from 'react-redux';
import { useDispatch } from 'react-redux';
import Bio_Page from './pages/Login';
import { ConnectionError } from './Connection/Connection';





function App() {
  let dispatch = useDispatch()
  let screenWidth = window.screen.width
  let currentPerson = useSelector(state => state.store.currentPerson)
  let currentUser = useSelector(state => state.store.currentUser)
  let postsDate = useSelector(state => state.store.postsDate)
  let isPosts = useSelector(state => state.store.isPosts)
  let usersDate = useSelector(state => state.store.usersDate)
  let mobileBio = useSelector(state => state.store.mobileBio)
  let isTrade = useSelector(state => state.store.isTrade)
  let isContacts = useSelector(state => state.store.isContacts)
  let currentItem = useSelector(state => state.store.currentItem)
  let register = useSelector(state => state.store.register)
  let isReg = useSelector(state => state.store.isReg)


  function getUs(arg, connection = true){
    if(arg){
      dispatch({type: 'SET_UD', item: arg})
    }
    if(!connection){
      dispatch({type:'SET_CN', item: false})
    } 
  }

  useEffect(()=>{
    getUsers(getUs, JSON.parse(localStorage.getItem('usersDate')))
    
  }, [])


  return(
  <Context.Provider value={{screenWidth, currentUser, mobileBio, currentPerson, isTrade, isPosts, postsDate, isContacts, currentItem, usersDate, isReg, register}}>
    <ConnectionError/>
    <Routes>
      <Route path='/' element={<Registrate/>}/>
      <Route path='/bio' element={<Bio_Page/>}/>
      <Route path='/treads' element={<Treads/>}/>
      <Route path='/general' element={<General/>}/>
      <Route path='/treadsBl' element={<TreadsBl/>}/>
      <Route path='*' element={<Registrate/>}/>
      
    </Routes>
  </Context.Provider>
  )
}
export default App;

