import { useContext } from "react"
import Context from "../../Context/Context"
import './img.css'

function Img(props){
    let {screenWidth} = useContext(Context)
    if(screenWidth >= 500){
        return (<img src={require(`../../${props.src}`)} alt=""/>)
    } 
    else{
        return (<img src={require(`../../${props.src}`)} alt="" className="mobile_bio_img"/>)
        
    } 

}
export default Img 


