import { useContext, useRef } from "react";
import Context from "../../Context/Context";
import addFriend from "../../hooks/addFriend";
import setUsers from "../../hooks/setUsers";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import './button.css'

function Button(props){
    let dispatch = useDispatch()
    const {usersDate, setUsersDate, currentPerson, currentUser, screenWidth} = useContext(Context)
    let isUser = usersDate.includes(currentUser)
    const navigate = useNavigate()
    let but = useRef()
    function butClick(){
        if(props.friend === 'Write'){
            dispatch({type:'SET_TR'})
            if(screenWidth < 500)  props.closeBio()
            navigate('/treads')
        }
        else{
            addFriend(usersDate, dates, currentUser, currentPerson, setUsers)
        }
    }

    function dates(arg){
        dispatch({type: 'SET_UD', item: arg})
    }

    if(true){
        return(
            <div className="bio_message">
                <div ref={but}  className="add_friend" onClick={()=> butClick()}>{props.friend}</div>
            </div>
        )
    }
}
export default Button