
import React, { useContext} from "react"
import Context from "../Context/Context"
import MobileBio from "./BioBloks/MobileBio"
import BioPc from "./BioBloks/BioPc"
import { haveUser } from "../hooks/haveUser"
import { useNavigate } from "react-router"

function Bio(){
    let navigate = useNavigate
    const {screenWidth} = useContext(Context)
    
    if(haveUser()){
        navigate('/general')
      }
 
    return screenWidth > 500 ? <BioPc/> : <MobileBio/>


    
}
export default Bio