import { useContext} from 'react';
import '../App.css';
import Bio from '../Bio/Bio';
import Contacts from '../Contacts/Contacts';
import React from 'react';
import Context from '../Context/Context';
import Main from '../Main/Main';
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import { CheckPage } from '../hooks/checkPage';

function Treads() {
  let {screenWidth, register, currentUser, isTrade, currentPerson} = useContext(Context)
  let dispatch = useDispatch()

  useEffect(()=>{
    dispatch({type: 'SET_TR'})
  }, [])

// CheckPage('treads', register, isTrade, currentUser, currentPerson)

  return (
    <>
        <Contacts/>
        <Main/>    
        {screenWidth > 500 ?<Bio />: ''}
          </>
  );
}

export default Treads;
