import { useContext, useEffect, useRef } from "react"
import Context from "../../../Context/Context"
import setUsers from "../../../hooks/setUsers"
import CheckIsAccaunt from "../../../hooks/checkAccaunt"
import { useDispatch } from "react-redux"
import './login.css';

function BioSignin(prop){
    let dispatch = useDispatch()
    const {setRegister, usersDate} = useContext(Context)
    let name = useRef()
    let login = useRef()
    let password = useRef()
    let img;
    let activeImg

    function setImg(e){
        activeImg ? activeImg.classList.remove('choosed_img') : activeImg = e;
        e.classList.add('choosed_img')
        img = `${e.id}`
        activeImg = e
    }

    
    async function Registrate(){
        let newUser = await {
            email: login.current.value,
            password: password.current.value,
            name: name.current.value,
            img: img,
            id: `000${usersDate.length + 1}`,
            friends: [],
            treads: {}
        }
        await dispatch({type: 'SET_UD', item: [...usersDate, newUser]})
        await setUsers([...usersDate, newUser])
        await prop.setRegister(false)
    }
    return(
        <div className="container">
        <h2 className="login_lable">Sign in</h2>
        <input ref={name} type="text" className="username_in bio_inputs" placeholder="Username"/>
        <input ref={login} type="text" className="login_in bio_inputs" placeholder="Login"/>
        <input ref={password} type="text" className="password_in bio_inputs" placeholder="Password"/>
        <div className="choose_img_dt">
            <img src={require('../../../imgs/user1.png')} onClick={(e)=> setImg(e.target)} id="imgs/user1.png" className="choose_img_item" alt=""/>
            <img src={require('../../../imgs/user2.png')} onClick={(e)=> setImg(e.target)} id="imgs/user2.png" className="choose_img_item" alt=""/>
            <img src={require('../../../imgs/Layer 13.png')} onClick={(e)=> setImg(e.target)} id="imgs/Layer 13.png" className="choose_img_item" alt=""/>
            <img src={require('../../../imgs/Layer 16.png')} onClick={(e)=> setImg(e.target)} id="imgs/Layer 16.png" className="choose_img_item" alt=""/>
            {/* <img src={require('../../../imgs/Layer 12.png')} onClick={(e)=> setImg(e.target)} id="imgs/Layer 12.png" className="choose_img_item" alt=""/> */}
            <img src={require('../../../imgs/Photo.png')} onClick={(e)=> setImg(e.target)} id="imgs/Photo.png" className="choose_img_item" alt=""/>
        </div>
        <div className="reg_unactive" onClick={()=> prop.setRegister(false)}>back</div>

            <button className="reg_but" onClick={()=> CheckIsAccaunt(login, name, usersDate, Registrate, setRegister)}>Reg</button>
        
    </div>
    )
}
export default BioSignin